import React, {Component} from 'react';
import './style.css';

import PropTypes from 'prop-types';
import TranslationColumn from './TranslationColumn/TranslationColumn';

export default class TranslationSection extends Component {
  render() {

    return (
        <section className="greyWrapper sectionBase">
          <div className="container">
            <div className="row">

              <div className="col-12">
                <div className="row translation__header">
                  <div className="col-md-4 col-12">
                    <div className="section-title">
                      <p>Servicii</p>
                      <h2 className="title">{this.props.mainHeader}</h2>
                    </div>
                  </div>
                  <div className="col-md-8 col-12">
                    <p>{this.props.mainText}</p>
                  </div>
                </div>
              </div>

              <div className="col-12">
                <div className="row translation__body">
                  <TranslationColumn {...this.props.juridic}/>
                  <TranslationColumn {...this.props.general}/>
                </div>
              </div>
            </div>
          </div>
        </section>
    );
  }
}

TranslationSection.propTypes = {
  mainText: PropTypes.string.isRequired,
  mainHeader: PropTypes.string.isRequired,
  juridic: PropTypes.shape({
    title: PropTypes.string.isRequired,
    items: PropTypes.object.isRequired,
  }),
  general: PropTypes.shape({
    title: PropTypes.string.isRequired,
    items: PropTypes.object.isRequired,
  })
};


