import React, {Component} from 'react';
import './style.css';

import PropTypes from 'prop-types';

export default class TranslationColumn extends Component {
  render() {
    const getItems = items => {
      return items.map((item, index) => (
          <li key={index}>
            <i className="fa fa-check-square-o mr-2"/>
            <span>{item}</span>
          </li>
      ));
    };

    return (
        <div className="col-sm-12 col-md-6">
          <h4 className="form-group">{this.props.title}</h4>
          <div className="row">
            <div className="col-6">
              <ul>
                {getItems(this.props.items.firstColumn)}
              </ul>
            </div>
            <div className="col-6">
              <ul>
                {getItems(this.props.items.secondColumn)}
              </ul>
            </div>
          </div>
        </div>
    );
  }
}

TranslationColumn.propTypes = {
  title: PropTypes.string.isRequired,
  items: PropTypes.shape({
    firstColumn: PropTypes.array.isRequired,
    secondColumn: PropTypes.array.isRequired,
  }),
};


