import React, {Component} from 'react';
import './Footer.css';
import logo from '../../../assets/images/logo_baltag_3.png';
import PropTypes from "prop-types";
import Links from "./Links/Links";
import Contact from "./Contact/Contact";

export default class Footer extends Component {
  render() {

    return (
        <footer className="mastFooter">
          <div className="inner">

            <div className="container">
              <div className="row">

                <div className="col-md-3 col-sm-12 order-sm-2">
                  <div className="logo"><a href="#">
                    <img src={logo} alt={this.props.logo.alt}/></a>
                  </div>
                  <p className="copyright">&copy; Mihaela Baltag 2018. All rights reserved.</p>
                </div>

                <div className="col-md-5 col-sm-6">
                 <Links {...this.props.navItems} footerTitle={this.props.footer.navigationTitle}/>
                </div>

                <div className="col-md-4 col-sm-6 order-sm-1">
                 <Contact {...this.props.contactInfo} footerTitle={this.props.footer.contactTitle}/>
                </div>

              </div>
            </div>
          </div>
        </footer>
    );
  }
}

Footer.propTypes = {
  navItems: PropTypes.object.isRequired,
  languages: PropTypes.array.isRequired,
  logo: PropTypes.shape({alt: PropTypes.string}),
  contactInfo: PropTypes.object.isRequired,
  footer: {
    navigationTitle: PropTypes.string.isRequired,
    contactTitle: PropTypes.string.isRequired,
    rights: PropTypes.object.isRequired
  }
};


