import React, {Component} from 'react';
import {Route, Switch, Redirect} from "react-router-dom";
import PropTypes from "prop-types";

import './style.css';
import Header from './Header/Header';
import Footer from './Footer/Footer';
import MainPage from "./MainPage/MainPage";
import ServicePage from "./ServicePage/ServicePage";

export default class App extends Component {
  render() {
    return (
        <div className="app">
          <Header headerInverse={this.props.headerInverse}
                  baseUrl={this.props.url}
                  data={this.props.marginals}/>

          <Switch>
            <Route path={`${this.props.url}/services`} render={() => <ServicePage {...this.props.content.services}/>}/>
            <Route path={`${this.props.url}/main`} render={() => <MainPage {...this.props.content.main}/>}/>
            <Redirect from={this.props.url} to={`${this.props.url}/main`}/>
          </Switch>

          <Footer {...this.props.marginals}/>
        </div>
    );
  }
}

App.propTypes = {
  headerInverse: PropTypes.bool.isRequired,
  url: PropTypes.string.isRequired,

  content: PropTypes.shape({
    services: PropTypes.object.isRequired,
    main: PropTypes.object
  }),
  marginals: PropTypes.object.isRequired
};



