import React, {Component} from 'react';
import PropTypes from 'prop-types';
import TranslationSection from "./TranslationSection/TranslationSection";
import Legalization from "./Legalization/Legalization";
import TitleSection from "./TitleSection/TitleSection";


export default class ServicePage extends Component {
  render() {
    return (
        <div>
          <TitleSection title={this.props.titleSection}/>
          <TranslationSection {...this.props.translation}/>
          <Legalization {...this.props.legalization}/>
        </div>
    );
  }
}

ServicePage.propTypes = {
  translation: PropTypes.object,
  legalization: PropTypes.object,
  titleSection: PropTypes.string
};


