import React, {Component} from 'react';
import './style.css';
import PropTypes from "prop-types";

export default class Links extends Component {
  render() {
    const services = this.props.services.subItems.map((data, index) => (
        <li key={index}><a href="#">{data.displayName}</a></li>
    ));

    return (
        <div>
          <h5>{this.props.footerTitle}</h5>
          <ul className="links">
            <div className="row">
              <div className="col-6">
                <ul>
                  {services}
                  <li><a href="#">{this.props.legalization.displayName}</a></li>
                </ul>
              </div>
              <div className="col-6">
                <ul>
                  <li><a href="#">{this.props.contact.displayName}</a></li>
                  <li><a href="#">{this.props.aboutMe.displayName}</a></li>
                  <li><a href="#">{this.props.collaborators.displayName}</a></li>
                </ul>
              </div>
            </div>
          </ul>
        </div>
    );
  }
}

Links.propTypes = {
  footerTitle: PropTypes.string.isRequired,

  legalization: PropTypes.object.isRequired,
  collaborators: PropTypes.object.isRequired,
  aboutMe: PropTypes.object.isRequired,
  contact: PropTypes.object.isRequired,
  services: PropTypes.shape({
    subItems: PropTypes.array.isRequired
  }),
};


