import React, {Component} from 'react';
import PropTypes from 'prop-types';
import NavLink from "./NavLink";

export default class NavItem extends Component {
  constructor(props) {
    super(props);
    this.state = {expanded: false,};

    this.toggleExpanded = this.toggleExpanded.bind(this);
  }

  toggleExpanded() {
    this.setState(prevState => ({expanded: !prevState.expanded}));
  }

  render() {
    const expanded = this.state.expanded ? ' open ' : '';
    const subNavItems = (this.props.subItems || []).map((child, i) =>
        <NavLink key={i}
                 displayName={child.displayName}
                 target={child.target}
                 route={this.props.baseUrl + this.props.route}/>
    );

    return (
        <NavLink toggleExpanded={this.toggleExpanded}
                 displayName={this.props.displayName}
                 target={this.props.target}
                 route={this.props.baseUrl + this.props.route}>
          {
            this.props.subItems
                ? <ul className={`dropMenu ${expanded}`}>{subNavItems}</ul>
                : null
          }

        </NavLink>

    );
  }
}

NavItem.propTypes = {
  baseUrl: PropTypes.string,

  displayName: PropTypes.string,
  target: PropTypes.string,
  route: PropTypes.string,

  subItems: PropTypes.arrayOf(PropTypes.shape({
    displayName: PropTypes.string,
    target: PropTypes.string,
    route: PropTypes.string,
  }))
};



